@import "../variables.scss";



* {
    margin : 0;
    padding :  0;
    box-sizing : border-box;
    font-family: "PT Sans",sans-serif;
}

#content  {
    line-height: 1.3;
    min-height: 60vh;
    padding : 1rem 2rem;
    display: flex;
    flex-direction: column;

    .main-title {
        text-align: center;
        font-size : 2rem;
    }


    ul {
        list-style-type: none;

        li {
            color : white;

            button {
                color : inherit;
            }
        }
    }

    .main-profile {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items : center;
        margin : 2rem 0rem;
        text-align: center;
        color : $font-color-base;
        font-weight: 600;
        font-size : 1.3rem;

        .greet-user {
            flex :  0 0  100%;
            margin-bottom : .5rem;
        }

        .email {
            padding-right : .5rem;
        }

        .age {
            padding : 0rem .5rem;
            border-left : 2px solid $base-color-pink;
            border-right : 2px solid $base-color-pink; 
        }

        .balance {
            padding-left : .5rem;
        }

        .favorite-tag-list {
            flex : 0 0 100%;
            margin-top: 2rem;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            li {
                font-size : 13.3px;
                margin-right : 1rem;
                align-self: flex-end;
                padding : .4rem .9rem;
                border-radius: 10px;
                background-color : #d43651;
            }
        }
    }

    .action-profile {
        display: flex;
        justify-content: center;
        align-items: flex-end;

        .btn-action {
            margin-right : 1rem;
            align-self: flex-end;
            padding : .4rem .9rem;
            border : none;
            outline : none;
            background-color : #25a1a1;
            border-radius: 10px;
        }
    }
}